.tl{
    margin-top: 1.563vw;
    display: grid;
    grid-template-areas: 'pgp tcs griet';
    .tl-i{
        margin-right: 1.042vw;
        &:last-child{
            margin-right: 0;
        }
    }
    .pgp-i{
        grid-area: pgp;
    }
    .tcs-i{
        grid-area: tcs;
    }
    .griet-i{
        grid-area: griet;
    }
    .tl-c{
        border-radius: $radius;
        width: 31.944vw;
        height: 45.313vw;
        padding: 2.604vw 2.214vw;
        color: $white;
        .tl-cc{
            box-sizing: border-box;
            position: relative;
            display: flex;
        }
        .tl-ci{
            opacity: 0;
            transform: translateY(-1.043vw);
            transition: all .66s cubic-bezier(0.66,0,0.2,1);
            visibility: hidden;
        }
        .tl-collapse{
            opacity: 1; 
            transform: translateY(0);
            transition-delay: .66s;
            visibility: visible;
        }
        .tl-cie{
            position: absolute;
            box-sizing: border-box;
            display: flex;
            flex-direction: column;
        }
        button{
            color: $white;
            border-color: $white;
        }
        h3{
            margin:1.042vw 0 0;
        }
        .tl-h{
            padding-bottom: 1.042vw;
            border-bottom: 0.156vw solid $white;
            margin:1.198vw 0 1.042vw;
            display: flex;
            justify-content: space-between;
            align-items: center;
            button{
                color: $white;
            }
        }
    }
    .pgp-g{
        background-color: $green;
        background-color: #BB6830;
        background-color:  #89b782;
        background-color: #e8cc3d;
        background-color: #d83030;
        background-color: #6d37ff;
        background: #049cb7;
        background: #91BEB7;
        background: #8eaf20;
        background: #34495E;
    }
    .pgp-d{

        background: #049cb7;
        // background: linear-gradient(135deg,#049cb7,#3dd9bd);
        background: #91BEB7;
        background: linear-gradient(135deg,#4f6f8f,#34495E);
    }
    .pgp-y{
        background-color: $yellow;
        background: #91BEB7;
    }
    .tcs-p{
        background-color: $lightpurple;
        // background-color: #6bc25c;
        // color: $black;
        background: #049cb7;
        // button{
        //     color: $black;
        //     border-color: $black;
        // }
        .tl-h{
            // border-bottom: 0.156vw solid $black;
            padding-bottom: 1.042vw;
            // button{
            //     border-color: $black;
            //     color: $black;
            // }
        }
    }
    .tcs-g{
        // background-color: $greengrey;
        background: linear-gradient(135deg,#049cb7,#3dd9bd);
    }
    .griet-p{
        // background-color: $purple;
        // background: linear-gradient(135deg,#1c7df3,#1abcfa);
        // background: linear-gradient(135deg,#0D1387,#867AF6);
        background: $yellow;
        // background-color: #f1c40f;
        background: #E39801;
        // background: #b0b791;
        // color: $black;
        // background-color:  #89b782;
        // background-color: #D0aebc;
        // background: #708c3e;
    }
    .griet-b{
        // background-color: $blue;
        // background: #027fff;
        // background: #0d1387;
        background: #ff6a18;
        background: #E39801;
        background: linear-gradient(135deg,#E39801,$yellow);
    }
}

@media screen and (max-width: 1280px) {
    .tl{
        margin-top: 2.345vw;
        display: grid;
        grid-template-areas: 'pgp tcs' 'griet griet';
        .tl-i{
            margin-right: 2.344vw;
            margin-bottom: 2.344vw;
            &:nth-child(2){
                margin-right: 0;
            }
            &:last-child{
                margin-right: 0;
                margin-bottom: 0;
            }
        }
        .tl-c{
            border-radius: $radius-d;
            max-width: 48.047vw;
            width: unset;
            height: 63.164vw;
            padding: 3.906vw 3.321vw;
            color: $white;
            h3{
                margin:1.563vw 0 0;
            }
            .tl-h{
                padding-bottom: 1.563vw;
                border-bottom: 0.234vw solid $white;
                margin:1.797vw 0 1.563vw;
            }
        }
        // .tcs-p{
        //     .tl-h{
        //         border-bottom: 0.234vw solid $white;
        //         // button{
        //         //     border-color: $black;
        //         //     color: $black;
        //         // }
        //     }
        // }
        .griet{
            max-width: 98.438vw;
            width: unset;
            height: 34.57vw;
        }
    }
}

